import clsx from "clsx";
import WithPadding from "components/shared/WithPadding";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import style from "./Hero.module.scss";

export default function Hero() {
  const navigate = useNavigate();
  const [hover, sethover] = useState(false);

  return (
    <WithPadding className={style.withPadding}>
      <div className={style.wrapper}>
        <div className={style.left}>
          <div className={style.header}>
            <div>Discover Your</div>
            <div>Future Today</div>
          </div>

          <div className={clsx(style.header, style.mobile)}>
            Discover Your Future Today
          </div>

          <div className={style.subheader}>
            <div>Unearth the career that's tailored just for you with</div>
            <div>the world's most advanced assessment.</div>
          </div>

          <div className={clsx(style.subheader, style.mobile)}>
            <div>
              Unearth the career that's tailored just for you with the world's
              most advanced assessment.
            </div>
          </div>

          <div className={style.cta}>
            <button
              type="submit"
              onClick={navigate.bind(this, "/auth/register")}
              onMouseEnter={() => sethover(true)}
              onMouseLeave={() => sethover(false)}
              className={hover ? style.button2 : null}
            >
              {hover ? "Start the Test" : "Discover Mine"}
            </button>
          </div>
        </div>
      </div>
    </WithPadding>
  );
}
