import WithPadding from "components/shared/WithPadding";
import { useEffect, useState } from "react";
import SwiperCore, { Autoplay, Pagination } from "swiper/core";
import { Swiper, SwiperSlide } from "swiper/react";
import { getRequest } from "utils/api";
import star from "../../assets/images/main/singleStart.png";
import style from "./Program2.module.scss";

SwiperCore.use([Pagination, Autoplay]);

export default function Program2() {
  const [reviews, setReviews] = useState([]);

  async function fetchReviews() {
    try {
      const res = await getRequest(`/web/reviews`, { noAuth: true });
      setReviews(res.data);
    } catch {
    } finally {
    }
  }

  function truncate(input) {
    const limit = 220;
    if (input.length > limit) {
      return input.substring(0, limit) + "...";
    }
    return input;
  }

  useEffect(() => {
    fetchReviews();
  }, []);

  return (
    <WithPadding className={style.wrapper}>
      <div className={style.header}>Navigating Careers Across Continents</div>
      <div className={style.sub_header}>
        From diverse backgrounds to shared success stories, discover the global
        impact of CareerTest.ai
      </div>

      <Swiper
        pagination={{ clickable: true }}
        navigation={false}
        slidesPerView={1}
        spaceBetween={10}
        className={style.swiperContainer}
        allowTouchMove={true}
        breakpoints={{
          320: {
            slidesPerView: 1,
            spaceBetween: 10,
          },
          600: {
            slidesPerView: 2,
            spaceBetween: 20,
          },
          960: {
            slidesPerView: 3,
            spaceBetween: 40,
          },
          1440: {
            slidesPerView: 4,
            spaceBetween: 50,
          },
        }}
        speed={1000}
        autoplay={{ delay: 3000 }} // Add autoplay configuration
        loop={true}
      >
        {reviews.map((item) => (
          <SwiperSlide className={style.swiperslide} key={item.name}>
            <div className={style.profile_photo}>
              <img src={item.photo} height={80} width={80} alt="photos" />
            </div>
            <div className={style.card_content}>
              <div className={style.name}>{item.name}</div>
              <div className={style.place}>{item.location}</div>

              {[...Array(item.rating)].map((i) => (
                <img key={i} src={star} height={32} width={32} alt="" />
              ))}

              <div className={style.about}>{truncate(item.content)}</div>
            </div>
          </SwiperSlide>
        ))}
      </Swiper>
    </WithPadding>
  );
}
